.list__container {
  margin: auto;
  width: 80%;
  text-transform: capitalize;
}
.list__container .list__header {}
.list__container .list__header h1 {
  font-size: 35px;
  color: #2E3532;
}
.list__container .list__content .list__content_actions {
  text-transform: lowercase;
}
.list__container .list__content {}
.list__container .list__content .list__content_entry h2{
  margin: 0;
  text-transform: lowercase;
}
.list__container .list__content .list__content_entry .list__content_buttons {
  display: flex;
  justify-content: flex-end;
}
.list__container .list__content .list__content_entry .list__content_buttons .list__button_submit {
  border: none;
  border-radius: 6px;
  background: #D3EFBD;
  color: #2E3532;
  padding: 12px 30px;
  font-weight: bold;
  cursor: pointer;
}
.list__container .list__content .list__content_entry {
  background: #D2D4C8;
  border-radius: 7px;
  padding: 10px 15px;
}
.list__container .list__content .list__content_entry h2 {}
.list__container .list__content .list__content_entry .list__separator {}
.list__container .list__content .list__content_entry label input {
  margin-top: 5px;
  border: none;
  padding: 10px;
  border-radius: 6px;
}
.list__container .list__content .list__content_entry label textarea {
  margin-top: 5px;
  border: none;
  padding: 10px;
  border-radius: 6px;
}
.list__container .list__content .list__content_entry label {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.list__container .list__content .list__content_entry .list__content_error {
  color: #8B2635;
  text-transform: initial;
  font-weight: bold;
}
.list__container .list__content .list__content_tasks {
  list-style: none;
  padding: 0;
}
.list__container .list__content .list__content_tasks li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #E0E2DB;
  margin-bottom: 15px;
  padding: 10px 5px;
  border-radius: 7px;
}
.list__container .list__content .list__content_tasks li .switch {
  width: 50px;
  margin-right: 15px;
}
.list__container .list__content .list__content_tasks li .title {
  flex-basis: 20%;
}
.list__container .list__content .list__content_tasks li .description {
  flex-basis: 50%;
}
.list__container .list__content .list__content_tasks li .list__content_delete{
  width: 50px;
  background: #8B2635;
  border:none;
  color: #E0E2DB;
  padding: 12px 0;
  transform: translateX(20px);
  border-radius: 6px;
  font-weight: 800;
  cursor: pointer;
}
.list__container .list__footer .list__button_reset {
  background: #8B2635;
  border:none;
  color: #E0E2DB;
  padding: 12px;
  border-radius: 6px;
  font-weight: 800;
  cursor: pointer;
}
.list__container .list__footer p {
  text-align: right;
}



/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
